'use strict'

const column = document.body.querySelector('div[data-cards="cards-container"]')
const counterBlock = document.body.querySelector('.board__to-do-counter')

let counter = column.children.length

export let countCards = (column, counterBlock) => {
    column.children ? counter = column.children.length : counter = 0
    counterBlock.innerHTML = counter
}

countCards(column, counterBlock) // running the function on page load
