'use strict'

const clearBtn = document.body.querySelector('.board__to-do-clear-btn')
const counterBlock = document.body.querySelector('.board__to-do-counter')
let cards = document.body.querySelector('.board__to-do-cards-container')

import { countCards } from './cardCounter.js'

clearBtn.addEventListener('click', () => {
    if(cards) {
        cards.innerHTML = ''
        countCards(cards, counterBlock)
    }
})

cards.addEventListener ('click', event => {
    if(event.target.classList.contains('board__card-remove-btn')
    || event.target.closest('.board__card-remove-btn')) {
        let card = event.target.closest('.board__card')
        card.remove()
        countCards(cards, counterBlock) // recount the amount of cards to display on the counter
    }
})

clearBtn.addEventListener('mouseover', () => clearBtn.innerHTML = 'X')// shows X while mouse is on the button
clearBtn.addEventListener('mouseout', () => countCards(cards, counterBlock)) // turns back to showing the actual number of cards
